a {
    display: flex;
    padding: 10px;
    margin: 10px;
    background: #3f3d56;
    min-width: 80px;
    max-width: 100px;
    justify-content: center;
    flex-wrap: wrap;
    border-radius: 20px;
    color: #e1ddf3;
    text-decoration: none;
}

a:hover {
    background-color: #a592f2;
    color: #3f3d56;
    cursor: pointer;
}

.card {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}