div.header {
    color: rgb(142, 142, 156);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-top: 50px;
}

img {
    width: auto;
    transition: max-width 1s, min-width 1s;
}

.img-exp {
    max-width: 350px;
    min-width: 200px;
}

.img-cont {
    max-width: 250px;
    min-width: 200px;
}

h1 {
    color: #e1ddf3;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    transition: font-size 1s;
    margin: 30px;
}

.h1-exp {
    font-size: 60px;
}

.h1-cont {
    font-size: 50px;
}